export default {
    props: {
        companyDetail: String
    },
    data() {
        return {
            isShow: true
        }
    },
    mounted(){
        var query = wx.createSelectorQuery().in(this)
        query.select('#word').boundingClientRect()
        query.selectViewport().scrollOffset()
        var that = this
        query.exec(function(res){
            let height = res[0].height
            let col = height/32
            if(col<=3){
                that.isShow = null
            }
        })
    }
}
